import {
    Link,
} from '@chakra-ui/react';

export default function ProjectLink(props) {
    return (
        <Link textDecoration='underline' _hover={{
            textDecoration: 'none',
            color: '#4be371'
        }}
        _focus={{ boxShadow: 'none!important' }}
        href={props.url} target='_blank'>
            {props.name}
        </Link>
    )
} 