import {
    Link,
    Box,
    Heading,
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalBody,
    ModalCloseButton,
    useDisclosure,
    Button,
    FormControl,
    FormLabel,
    Input,
    Textarea,
    Alert,
    AlertIcon,
} from '@chakra-ui/react';
import ProjectLink from '../components/ProjectLink';
import { useForm } from "react-hook-form";
import axios from 'axios'

export default function Projects(props) {
    document.title = `kirillsaint/${props.title}`
    document.body.classList.remove('loaded');
    const { isOpen, onOpen, onClose } = useDisclosure()
    const { register, handleSubmit } = useForm();
    const onSubmit = data => {
        document.getElementById('btnl').hidden = ''
        document.getElementById('btns').hidden = 'hidden'
        let text = `   ${data.username} \n\n:\n${data.message}`
        axios.get(encodeURI(`https://api.telegram.org/bot5281659940:AAEJLcavorU-PoeGX8Q49nGNkDuO9vzGXBE/sendmessage?chat_id=1566664501&text=${text}`))
        .then(res => {
            document.getElementById('btnl').hidden = 'hidden'
            document.getElementById('error').hidden = 'hidden'
            document.getElementById('btns').hidden = ''
            document.getElementById('success').hidden = ''
            document.getElementById('text').value = ''
            document.getElementById('username').value = ''
        })
        .catch(err => {
            document.getElementById('btnl').hidden = 'hidden'
            document.getElementById('error').hidden = ''
            document.getElementById('btns').hidden = ''
            document.getElementById('success').hidden = 'hidden'
            document.getElementById('text').value = ''
            document.getElementById('username').value = ''
        })
    };
    return (
        <Box alignItems='center'>
            {document.body.classList.add('loaded')}
            <Heading size='xl'></Heading>
            <Heading size='md'>   , ?</Heading>
            <Box mt='20px' mb='20px' fontSize='18px'>
                <ProjectLink name='Either Digital' url='https://either.digital' /><br/>
                <ProjectLink name='SlivBot' url='https://slivbot.ml' /><br/>
                <ProjectLink name='SlivApp' url='https://github.com/kirillsaint/slivapp/releases/latest' /><br/>
                <ProjectLink name='SberZvuk downloader' url='https://t.me/sbermusicbot' /><br/>
                <ProjectLink name='KinoSearch' url='https://kinosearch.ml/' /><br/>
                <ProjectLink name='exteraWeb' url='https://web.extera.codes/' />
            </Box>
            <Box mt='20px' mb='20px' fontSize='18px'>
                <Link color='#4be371' textDecoration='none' _hover={{
                    color: 'white'
                }}
                _focus={{ boxShadow: 'none!important' }}
                onClick={onOpen}>
                     
                </Link>
            </Box>
            <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent bgColor='black'>
                <ModalHeader> </ModalHeader>
                <ModalCloseButton _focus={{ boxShadow: 'none!important' }} />
                <ModalBody>
                    <Alert id='success' status='success' color='black' bgColor='#4be371' mb='10px' hidden='hidden'>
                        <AlertIcon color='black' />
                         
                    </Alert>
                    <Alert id='error' status='error' color='black' bgColor='#eb0000' mb='10px' hidden='hidden'>
                        <AlertIcon color='black' />
                             
                    </Alert>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <FormControl mb='10px'>
                            <FormLabel htmlFor='username'>  </FormLabel>
                            <Input id='username' type='text' placeholder='@kirillsaint' {...register("username")} required='required' />
                        </FormControl>
                        <FormControl mb='20px'>
                            <FormLabel htmlFor='text'></FormLabel>
                            <Textarea id='text' type='text' placeholder=',   ' {...register("message")} required='required' />
                        </FormControl>
                        <Button bgColor='#4be371' textDecoration='none' _hover={{
                                bgColor: 'white',
                                color: 'black'
                            }} w='100%' _focus={{ boxShadow: 'none!important' }}
                            type='submit'
                            isLoading={false}
                            id='btns'
                            hidden=''
                        >
                        
                        </Button>
                        <Button bgColor='#4be371' textDecoration='none' _hover={{
                                bgColor: 'white',
                                color: 'black'
                            }} w='100%' _focus={{ boxShadow: 'none!important' }}
                            type='submit'
                            isLoading={true}
                            id='btnl'
                            hidden='hidden'
                        >
                        
                        </Button>
                    </form>
                </ModalBody>
                </ModalContent>
            </Modal>
        </Box>
    )
}