import {
    Link,
    Box,
    Heading,
    Img,
    Center,
    Flex
} from '@chakra-ui/react';
import axios from 'axios';
import '../css/socials.css';

export default function Main(props) {
    document.title = `kirillsaint/${props.title}`
    document.body.classList.remove('loaded');
    axios.get('https://api.telegram.org/bot5281659940:AAEJLcavorU-PoeGX8Q49nGNkDuO9vzGXBE/getUserProfilePhotos?user_id=1566664501')
        .then(res => {
            let avaId = res.data.result.photos[0][2].file_id
            axios.get(`https://api.telegram.org/bot5281659940:AAEJLcavorU-PoeGX8Q49nGNkDuO9vzGXBE/getFile?file_id=${avaId}`)
            .then(data => {
                let avaUrl = 'https://api.telegram.org/file/bot5281659940:AAEJLcavorU-PoeGX8Q49nGNkDuO9vzGXBE/' + data.data.result.file_path
                document.getElementById('ava').src = avaUrl
                document.body.classList.add('loaded');
            })
        })
    return (
        <Box alignItems='center'>
            <Center>
            <Img src='' id='ava' w='10em' h='10em' borderRadius='5.6em' mb='10px'></Img>
            </Center>
            <Heading size='xl'>kirillsaint</Heading>
            <Heading size='md'>,  ?</Heading>
            <Center>
                <Flex id='socials'>
                    <Link href="https://t.me/kirillsaint" target='_blank'><i className="bi bi-telegram social"></i></Link>
                    <Link href="https://github.com/kirillsaint" target='_blank'><i className="bi bi-github social" _focus={{ boxShadow: 'none!important' }}></i></Link>
                    <Link href="https://vk.com/kirillsaint" target='_blank' _focus={{ boxShadow: 'none!important' }}><i className="fa-brands fa-vk social"></i></Link>
                </Flex>
            </Center>
        </Box>
    )
}